  .title {
    margin: 50px auto 0 auto;
    text-align: center;
    color: #fff;
    font-size: 32px;
  }
  
  .gallery.box {
    width: 280px;
    /* width: 320px;
    height: 405px; */
    margin: 10px auto;
    overflow: hidden;
    border-radius: 1%;
    border: 5px #fff solid;
  }
  
  .gallery .card {
    position: relative;
  }
  
  .gallery .background,
  .gallery .column {
    height: 405px;
  }
  
  .gallery .overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    opacity: 0;
    background-color: #000;
    transition: opacity 0.35s;
  }
  
  .gallery .info,
  .gallery .foot {
    transition: bottom 0.35s;
  }
  
  .gallery .info {
    position: absolute;
    bottom: -35px;
    width: 100%;
  }
  
  /* .gallery .info h2 a { */
  .gallery .info h2 {
    color: #fff;
    display: block;
    text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);
    text-decoration: none;
  }
  
  .gallery .info h1 {
    color: #fff;
    display: block;
    /*text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.4);*/
    text-decoration: none;
    font-size: 26px;
    margin-left: 10px;
  }
  
  .gallery .info h2 {
    line-height: 24px;
    font-size: 18px;
    padding: 0 10px;
    font-weight: 400;
  }
  
  .gallery .foot {
    height: 35px;
    bottom: -35px;
    position: relative;
    color: #fff;
    padding: 0 10px;
  }
  
  .gallery .foot .social {
    float: right;
    color: #fff;
    font-size: 20px;
    margin-left: 20px;
  }
  
  .gallery .foot .line {
    height: 2px;
    display: block;
    margin: 10px 0;
    background-color: #fff;
    margin-top: 20px;
    width: 0%;
    transition: width 0.6s;
  }
  
  .gallery .date {
    font-weight: 300;
    color: white;
  }
  .gallery .social {
    font-weight: 300;
    text-decoration: none;
  }
  
  .gallery .fa-camera-retro {
    top: 0;
    left: 0;
    padding: 10px 10px;
    position: absolute;
    color: #fff;
    font-size: 24px;
    z-index: 1;
    transition: font-size 0.35s;
  }
  
  /* All hovers here! */
  .gallery:hover .info,
  .gallery:hover .foot {
    bottom: 10px;
  }
  
  .gallery:hover .overlay {
    opacity: 0.5;
  }
  
  .gallery:hover .foot .line {
    width: 100%;
  }
  
  .gallery:hover .fa-camera-retro {
    font-size: 32px;
  }
  
